<template>
  <main id="container">
    <div class="wrapper" v-if="showGame">
      <div id="game-container"></div>
    </div>
  </main>
</template>

<script>
  import Game from './js/game-controller.js'

  export default {
    name: 'game',
    data () {
      return {
        showGame: false,
        cursor: 0,
        winningCombo: [38, 38, 40, 40, 37, 39, 37, 39, 66, 65, 13]
      }
    },
    methods: {
      runGame () {
        Game.Run()
      },
      checkKey (e) {
        if (e.keyCode === this.winningCombo[this.cursor]) {
          this.cursor++
          if (this.cursor === this.winningCombo.length) {
            this.showGame = true
            this.runGame()
          }
        } else {
          this.cursor = 0
        }
      },
      stopGame () {
        this.showGame = false
        // Game.Stop()
      }
    },
    created () {
      // this.runGame()
      window.addEventListener('keyup', this.checkKey)
    }
}
</script>
<style scoped lang="scss">
.wrapper{
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 100;
  margin-left: auto;
  margin-right: auto;
  background-color: rgba(0, 0, 0, 0.95);
  #game-container{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}
</style>



