<template>
  <div class="blog-list" v-bind:ref="'blogList'">
    <transition name="div-fade" mode="out-in">
      <div v-if="!loading">
        <blog-post :title="blog.title" :age="parseDate(blog.published)" :description="blog.content" v-for="blog in displayedBlogs" :key="blog.$index" :labels="blog.labels" :selectedLabel="selectedLabel" v-on:sort="sortByLabel"></blog-post>
      </div>
      <loading-spinner v-else></loading-spinner>
    </transition>
    <blog-pagination :total="this.blogs.length" @clicked="paginationClick"></blog-pagination>
  </div>
</template>

<script>
import BlogPost from './BlogPost'
import BlogPagination from './BlogPagination'
import api from '../../gateways/blogger'
import MomentTZ from 'moment-timezone'
import LoadingSpinner from '../global/Loading'
export default {
  name: 'bloglist',
  components: {BlogPost, BlogPagination, LoadingSpinner},
  methods: {
    orderList: function (blogLabel) {
      this.orderedBlogs.sort(function (a, b) {
        if (a.labels && !b.labels) {
          if (a.labels.indexOf(blogLabel) !== -1) {
            return -1
          }
        }
        if (!a.labels && b.labels) {
          if (b.labels.indexOf(blogLabel) !== -1) {
            return 1
          }
        }
        if (a.labels && b.labels) {
          var aIndex = a.labels.indexOf(blogLabel)
          var bIndex = b.labels.indexOf(blogLabel)
          if (aIndex !== -1 && bIndex !== -1) {
            a = new Date(a.published)
            b = new Date(b.published)
            return a > b ? -1 : a < b ? 1 : 0
          }
          return bIndex - aIndex
        }
        return 0
      })
    },
    orderLabels: function (inputLabel) {
      this.displayedBlogs.forEach(function (blog) {
        if (blog.labels) {
          blog.labels.sort(function (x, y) {
            return x === inputLabel ? -1 : y === inputLabel ? 1 : 0
          })
        }
      })
    },
    sortByLabel: function (label) {
      if (label === '') {
        label = 'pinned'
      }
      this.selectedLabel = label
      this.orderedBlogs = this.blogs.slice(0)
      this.orderList(label)
      this.populateDisplay()
      this.focusTopOfList()
    },
    focusTopOfList: function () {
      this.$refs['blogList'].scrollIntoView()
      this.$refs['blogList'].setAttribute('tabindex', 0)
      this.$refs['blogList'].focus()
      this.$refs['blogList'].removeAttribute('tabindex')
    },
    parseDate: function (dateStr) {
      return MomentTZ.tz(dateStr, MomentTZ.tz.guess()).format('MMM D, YYYY')
    },
    populateDisplay: function () {
      this.displayedBlogs = this.orderedBlogs.slice(this.offset, this.offset + 3)
      this.orderLabels(this.selectedLabel)
    },
    paginationClick: function (newPosition) {
      this.offset = newPosition * 3
      this.populateDisplay()
      this.focusTopOfList()
    }
  },
  data () {
    return {
      offset: 0,
      selectedLabel: 'pinned',
      displayedBlogs: [],
      orderedBlogs: [],
      blogs: [],
      loading: false
    }
  },
  created: function () {
    this.loading = true
    api.getBlogPosts().then(function (results) {
      this.blogs = results.body.items
      this.orderedBlogs = this.blogs.slice(0)
      this.orderList(this.selectedLabel)
      this.populateDisplay()
      this.loading = false
    }.bind(this))
  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import '../../variables.scss';
.blog-list{
  background-color: $dark-purple;
  padding: $home-section-padding;
  flex: 1 0 auto;
}
</style>



